import "babel-polyfill";
import Vue from "vue";
import VTooltip from "v-tooltip";
import VueRouter from "vue-router";
import Ajax from "../../../../web-ui/vue/ajax";
import Login from "./app/login.vue";
import Recover from "./app/recover.vue";
import Instructions from "./app/instructions.vue";
import Reset from "./app/reset.vue";
import Emails from "./app/emails.vue";
import SetNew from "./app/setNew.vue";
import Setup from "./app/setup.vue";
import PasswordPage from "./app/passwordPage.vue";
import SetupPassword from "./app/setupPassword.vue";
import SetupQuestions from "./app/setupQuestions.vue";

Vue.use(Ajax, {
  handleUnautorized: true
});
Vue.use(VTooltip);
Vue.use(VueRouter);
Vue.prototype.$reformatUrl = function (pageName) {
  let redirectRequired = false;
  let search = window.location.search;
  if (search != null) {
    let queryString = search.split("?")[1];
    if (typeof queryString != "undefined") {
      redirectRequired = true;
    }
  }
  if (redirectRequired) {
    window.location.href = "/#/" + pageName + search;
  }
};
Vue.prototype.$validateEmail = function (email) {
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};
new Vue({
  el: "#app",
  router: new VueRouter({
    routes: [
      { path: "*", name: "Login", component: Login },
      { path: "/Reset", name: "Reset", component: Reset },
      { path: "/Recover", name: "Recover", component: Recover },
      { path: "/Emails", name: "Emails", component: Emails },
      {
        path: "/SetupPassword",
        name: "SetupPassword",
        component: SetupPassword
      },
      {
        path: "/SetupQuestions",
        name: "SetupQuestions",
        component: SetupQuestions,
        props: true
      },
      {
        path: "/Instructions",
        name: "Instructions",
        component: Instructions,
        props: true
      },
      { path: "/SetNew", name: "SetNew", component: SetNew, props: true }
    ]
  })
});
